<template>
	<!-------------------Header wrap-------------------->
	<HeaderComponent />
	
	<!-----------------Company Icon & Search Bar wrap---------------->
	<SearchComponent />
	
	<!----------------Navigator wrap----------------->
	<NavComponent />

    <div class="productDisplay">
        <div class="picture">
            <img src="" alt="图片缺失" id="images">
        </div>
        <div class="content">
            <div class="productName">产品名称： {{productInfo.name}}</div>
            <div class="brand">商品品牌： {{productInfo.brand}}</div>
            <div class="desContent">
                <div class="descTitle">产品描述： </div>
                <div class="description">{{productInfo.description}}</div>
            </div>
            <div class="priceContent">
                <div class="priceTitle">零售价格： </div>
                <div class="productPrice">￥{{productInfo.price}}</div>
            </div>
            <div class="otherDetails">
                <div class="quantity">库存存货： {{productInfo.quantity}}</div>
                <div class="special" v-if="!hasSpecial"> 特价价格： 无</div>
                <div class="special" v-else>特价价格： ￥{{productInfo.special}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    import {useRoute} from 'vue-router'
    import {reactive, toRefs} from '@vue/reactivity'
    import {get} from '../../utils/reqEncap'
    import HeaderComponent from '../../components/HeaderComponent.vue'
    import SearchComponent from '../../components/SearchComponent.vue'
    import NavComponent from '../../components/NavComponent.vue'

    function handleProductEvent() {
        const reactiveData = reactive({
            productInfo: {},
            hasSpecial: false
        })
        let singleProductId = localStorage.getItem('singleProductId');
        async function getProductInfo() {
            const productData = await get(`/api/user/product/${singleProductId}`);
            reactiveData.productInfo = productData.data
            
            if(productData.data.special != '' || productData.data.special != null) {
                reactiveData.hasSpecial = true
            }else{
                reactiveData.hasSpecial = false
            }
        }
        const {productInfo, hasSpecial} = toRefs(reactiveData)
        return {productInfo, hasSpecial, getProductInfo}
    }

    export default {
        name: "SingleProduct",
        components: { HeaderComponent, SearchComponent, NavComponent },
        setup() {
            const {productInfo, hasSpecial, getProductInfo} = handleProductEvent();
            getProductInfo();

            return {productInfo, hasSpecial, getProductInfo};
        }
    }
</script>

<style lang="scss">
    .productDisplay {
        margin-left: 20%;
        margin-top: 50px;
        padding: 20px;
        width: 70%;
        background-color: rgb(252, 252, 252);
        display: flex;
        .picture {
            width: 30%;
            height: 400px;
            border: 1px solid rgb(199, 199, 199);
        }
        .content {
            margin-left: 10%;
            width: 45%;
            .productName {
                width: 100%;
                height: 50px;
                font-size: 200%;
                font-weight: bold;
            }
            .brand {
                margin: 30px auto;
                width: 100%;
                font-size: 150%;
            }
            .desContent {
                display: flex;
                margin-top: 50px;
                width: 100%;
                .descTitle {
                    width: 20%;
                    height: 50px;
                    font-size: 120%;
                    font-weight: bold;
                }
                .description {
                    width: 79%;
                    height: 80px;
                    font-size: 100%;
                }
            }
            .priceContent {
                display: flex;
                // margin-top: 50px;
                width: 100%;
                .priceTitle {
                    width: 20%;
                    height: 50px;
                    margin-top: 10px;
                    font-size: 120%;
                    font-weight: bold;
                    color: rgb(190, 3, 3);
                }
                .productPrice {
                    width: 79%;
                    height: 80px;
                    font-size: 200%;
                    font-weight: bold;
                    color: rgb(190, 3, 3);
                }
            }
            .otherDetails {
                display: flex;
                width: 100%;
                .quantity {
                    width: 49%;
                    height: 50px;
                    margin-top: 10px;
                    font-size: 120%;
                    // font-weight: bold;
                    // color: rgb(190, 3, 3);
                }
                .special {
                    width: 49%;
                    height: 50px;
                    margin-top: 10px;
                    font-size: 120%;
                    // font-weight: bold;
                    // color: rgb(190, 3, 3);
                }
            }
        }
    }
</style>